import {getTopList} from '../api/rank.js'
import scroll from '../components/scroll/scroll'
export default {
  data () {
    return {
      topList: []
    }
  },

  components: {
    scroll
  },

  methods: {},

  computed: {},
  created() {
    getTopList().then((res) => {
      if (res.code === 200) {
        this.topList = res.data
      } else {
        this.$toast('获取排行失败,请重试!')
      }
    })
  },
  mounted() {}
}




